.news {
    padding: 0 15px;
    margin-top: 20px;

    &__list {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 15px;

        padding: 15px 0;
    }

    &__item {
        background-color: #fff;
        border-radius: 5px;

        box-shadow: 0 0 5px rgba(173, 173, 173, 0.4);

        &:hover {
            box-shadow: 0 0 10px rgba(173, 173, 173, 0.5);
        }
    }

    &__cover {
        border-radius: 5px 5px 0 0;
        width: 100%;
        height: 150px;
        object-fit: cover;
    }

    &__item-wrapper {
        padding: 15px;
    }

    &__item-title {
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;

        overflow: hidden;
        display: -webkit-box;
        text-overflow: ellipsis;
        line-height: 16px;
        max-height: 32px;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }

    &__item-text {
        margin: 7px 0 15px 0;
        font-size: 12px;

        overflow: hidden;
        display: -webkit-box;
        text-overflow: ellipsis;
        line-height: 16px;
        max-height: 64px;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
    }

    &__item-footer {
        display: flex;
        justify-content: space-between;
    }

    &__item-date {
        display: flex;
        align-items: center;
        color: var(--gray);
        font-size: 14px;
    }

    &__item-icon {
        fill: var(--gray);
        width: 15px;
        height: 15px;

        margin-right: 5px;
    }

    &__button-more {
        padding: 8px 25px;

        border: 1px solid var(--light-gray);
        border-radius: 3px;

        color: var(--gray);
        background-color: transparent;

        font-size: 14px;

        &:hover {
            color: #000;

            border: 1px solid rgba(255, 200, 1, 0.5);
            background-color: $yellow;
            box-shadow: 0 0 10px rgba(255, 200, 1, 0.5);
            transition: .4s;
        }
    }

    &__all-button-link {
        display: flex;
        justify-content: center;

        text-decoration: none;
    }

    &__all-button {
        padding: 12px 45px;

        font-size: 16px;
        color: var(--dark-gray);

        background-color: var(--light-gray);
        border-radius: 3px;

        &:hover {
            color: #000;
            background-color: $yellow;
            box-shadow: 0 0 10px rgba(255, 200, 1, 0.5);
            transition: .4s;
        }
    }
}


@include rmin($sm-phone) {
    .news {
        &__item-title {
            font-size: 16px;
        }

        &__item-text {
            font-size: 14px;
        }

        &__cover {
            height: 200px;
        }

        &__item-wrapper {
            padding: 20px;
        }

        &__item-title {
            font-size: 16px;
        }
    
        &__item-text {
            font-size: 14px;
        }
    }
}

@include rmin($tablet) {
    .news {
        margin-top: 60px;
        
        &__cover {
            height: 200px;
        }

        &__list {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 25px;
    
            padding: 25px 0;
        }
        
    }
}

@include rmin($desktop) {
    .news {
        &__list {
            grid-template-columns: 1fr 1fr 1fr;
        }
    }
}
