.site-header {
    background: $yellow;

    display: flex;
    align-items: center;
    justify-content: space-between;

    padding: 0;

    // background-color: #fb0;
    height: 100px;

    &__nav-search-wrapper {
        display: flex;
    }

    // navigation
    &__nav {

        &-btn {}
    }

    // search
    &__search {
        margin-left: 10px;

        margin-right: 75px;

        &-btn {
            display: flex;
        }
    }

    // logo
    &__logo-wrapper {
        display: flex;
        margin: 0 40px 0 auto;
    }

    &__logo {
        display: none;

        &-phone {
            width: 125px;
        }
    }

    // social
    &__social {
        display: none;
        align-items: center;

        &-item {}

        &-link {
            display: flex;

            text-decoration: none;
            font-size: 28px;

            border-radius: 50%;

            & i {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 45px;
                height: 45px;
            }

            & .fab {
                color: #fff;

                &:hover {
                    transition: .5s;
                    color: $yellow;
                }
            }

            &:hover {
                transition: .5s;
                background-color: #fff;
            }
        }
    }

    &__donate-link {
        margin-left: 5px;
        padding: 5px 22px;

        color: #fff;
        font-weight: 700;
        font-size: 16px;

        text-decoration: none;

        border: 3px solid rgba(255, 255, 255, 0.5);
        border-radius: 25px;

        &:hover {
            color: $yellow;
            background-color: #fff;
            transition: .5s;
        }
    }
}


@include rmin($sm-phone) {
    .site-header {}
}

@include rmin($tablet) {
    .site-header {
        padding: 0 40px 0 0;

        // logo
        &__logo-wrapper {
            display: flex;
            margin: 0 auto;
        }

        &__logo {
            display: block;
            width: 250px;
            transform: scale(1);
            transition: all 0.3s;


            &-phone {
                display: none;
            }

            &:hover {
                transition: all 0.4s;
                transform: scale(1.1);
            }
        }

        &__social {
            display: flex;
        }
    }
}

@include rmin($desktop) {
    .site-header {
        padding: 0 40px 0 280px;

        &__logo {
            width: 325px;
        }
    }
}
