.player {
    background: linear-gradient(to top, $orange, $yellow);

    display: flex;
    justify-content: center;
    flex-direction: column;

    padding: 0 15px;
    height: calc(100vh - 70px);

    &__main {
        display: flex;
        align-items: center;
        justify-content: center;

        margin-top: auto;
    }

    &__previous-song {
        display: none;
    }

    &__next-song {
        display: none;
    }

    &__current-song {
        display: flex;
        align-items: center;
        flex-direction: column;

        margin: 0 100px;
    }

    &__btn {
        background-color: transparent;
    }

    &__icon {
        width: 100px;
        height: 100px;
        fill: #fff;
        opacity: .8;

        &:hover {
            opacity: .9;
        }
    }

    &__volume {}

    &__genre {
        overflow-x: scroll;
        scroll-snap-type: x mandatory;

        display: flex;
        flex-wrap: nowrap;

        padding: 0;
        margin: auto 0 45px;

        &-item {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            margin: 0 15px 15px 0;

            min-width: 115px;
            min-height: 100px;
            border-radius: 3px;
            background-color: white;

            text-align: center;

            &-play {
                width: 25px;
                height: 25px;
                fill: $orange;
                opacity: .5;
            }

            &-title {
                font-size: 14px;
                margin-top: 5px;
                color: #555555;
            }
        }
    }
}

.station {
    // z-index: 99;
    position: relative;
    width: 100%;
    height: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
    box-sizing: border-box;
}

.title {
    display: flex;
    font-size: 2vw;
    color: #fff;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.33);
}

.subtitle {
    display: inline-block;
    vertical-align: middle;
}

.live {
    display: flex;
    align-items: center;
    margin-left: 5px;
    padding: 0 10px;

    font-size: 10px;
    background-color: #cc1919;
    border-radius: 3px;
    font-weight: bold;
    color: #fff;
    text-align: center;
    opacity: 0;
    text-shadow: none;
}

@include rmin($sm-phone) {
    .player {
        &__genre {
            margin: auto 0 45px;

            &-item {
                min-width: 125px;
                min-height: 125px;
            }
        }
    }
}

@include rmin($tablet) {
    .player {
        padding: 0;
        height: calc(100vh - 100px);

        &__previous-song {
            display: block;
            text-align: center;
            margin-left: 25px; 
            
            & h2 {
                color: #fff;
                font-size: 18px;
                font-weight: 700;
            }

            & p {
                color: #3b3b3b;
                font-size: 14px;
            }
        }

        &__next-song {
            display: block;
            text-align: center;
            margin-right: 25px;
            
            & h2 {
                color: #fff;
                font-size: 18px;
                font-weight: 700;
            }

            & p {
                color: #3b3b3b;
                font-size: 14px;
            }
        }

        &__genre {
            overflow: hidden;

            display: flex;
            justify-content: center;

            padding: 0;
            margin: auto 0 25px;

            &-item {
                margin: 0 7px 15px;

                // min-width: 150px;
                // min-height: 150px;
            }
        }
    }
}

@include rmin($desktop) {
    .player {
        &__genre {
            &-item {
                margin: 0 15px 15px;
            }
        }
    }
}




