@import "node_modules/@glidejs/glide/src/assets/sass/glide.core";
@import "node_modules/@glidejs/glide/src/assets/sass/glide.theme";

.slider {
  width: 100%;
  margin: 0 auto;

  &__wrapper {
    width: 100%;
  }

  &__link {
    display: block;
  }

  &__item {
    width: 100%;
    height: 250px;
    object-fit: cover;
  }

  &__glide {
    position: relative;
  }

  &__glide-arrows {
    position: absolute;
    top: 45%;
    width: 100%;
  }

  &__controls {
    position: absolute;
    background-color: transparent;

    cursor: pointer;

    &--left {
      left: 15px;      
      transform: rotate(180deg); 
    }
    &--right {
      right: 15px;
    }
  }
  
  &__arrow {
    fill: #fff;
  }
}

@include rmin($tablet) {
  .slider {
    &__controls {
      &--left {
        left: 25px;      
      }
      &--right {
        right: 25px;
      }
    }
  }
}


@include rmin($desktop) {
  .slider {
    
  }
}