@import url('https://fonts.googleapis.com/css?family=Titan+One');
@import url('https://fonts.googleapis.com/css?family=Rubik+Mono+One&display=swap');

$pink : #ff006a;
$purple : #7b00e0;
$aqua : #66e6ff;
$font : 'Rubik Mono One', sans-serif;


@mixin base-font($size) {
    font-size: $size;
    font-family: $font;
}

.body-scroll {
    overflow: hidden;
}

.container {
    width: 100%;
    height: 100vh;
    margin: 0 auto;
    overflow: hidden;
}

.menu-container {
    background-color: $yellow;
    border-radius: 100%;
    color: white;
    cursor: pointer;
    position: absolute;
    width: 0;
    height: 0;
    left: -120px;
    top: -120px;
    transition: all 0.3s;

    &.full-menu {
        z-index: 100;
        border-radius: 0;
        padding: 0 !important;
        position: fixed;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        transition: all 0.3s;
        overflow: hidden;
    }
}

.fa-times {
    position: fixed;
}

.full-menu .menu {
    top: 40px;
    left: 40px;
}

.menu {
    color: white;
    font-size: 2.5em;
    position: absolute;
    top: 149px;
    left: 160px;
    z-index: 900;
    transition: all 0.3s;

    i {
        opacity: 0.8;
        transform: scale(1);
        transition: all 0.3s;

        &:hover {
            opacity: 1;
            transform: scale(1.2);
            transition: all 0.3s;
        }
    }
}

.overlay {
    margin: 100px 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 0%;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;

    &.open {
        z-index: 100;
        // opacity: .9;
        opacity: 1;
        visibility: visible;
        height: 85%;

        li {
            animation: fadeInRight .5s ease forwards;
            animation-delay: .35s;

            &:nth-of-type(2) {
                animation-delay: .4s;
            }

            &:nth-of-type(3) {
                animation-delay: .45s;
            }

            &:nth-of-type(4) {
                animation-delay: .50s;
            }
        }
    }

    nav {
        @include base-font(1.8em);
        position: relative;
        height: 50%;
        top: 50%;
        transform: translateY(-50%);
        font-weight: 400;
        text-align: center;
    }

    ul {
        list-style: none;
        padding: 0;
        margin: 0 auto;
        display: inline-block;
        position: relative;
        height: 100%;

        li {
            display: block;
            height: 20%;
            // height: calc(100% / 5);
            // min-height: 50px;
            position: relative;
            opacity: 0;

            a {
                display: block;
                position: relative;
                color: #fff;
                text-decoration: none;
                overflow: hidden;
                opacity: 0.7;
                transform: scale(1);
                transition: all 0.3s;

                &:hover,
                &:focus,
                &:active {
                    opacity: 1;
                    transform: scale(1.2);
                    transition: all 0.3s;
                }
            }
        }
    }
}

@keyframes fadeInRight {

    0% {
        opacity: 0;
        left: 20%;
    }

    100% {
        opacity: 1;
        left: 0;
    }
}
