.footer {
    background-color: #262626;
    padding: 40px 0 20px 0;

    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &__list-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        margin: 0 5px;
        padding: 10px;

        &:nth-child(1) {
            width: 100%;
            align-items: center;
        }

        &:not(:first-child) {
            width: 40%;
        }
    }

    &__logo {
        width: 75%;
    }

    &__donate-link {
        display: flex;
        justify-content: center;
        text-align: center;
        padding: 10px 15px;
        font-size: 12px;

        &:hover {
            color: #262626;
        }
    }

    &__copyright {
        display: flex;
        margin: 5px 0 15px;

        text-align: center;
        color: #fff;
        font-size: 13px;
        font-weight: 700;
    }

    // sublist
    &__sublist {
        &-item {
            margin: 5px 0;
            font-size: 14px;

            &:first-child {
                margin-bottom: 10px;

                color: #fff;
                font-size: 14px;
                font-weight: 700;
                text-transform: uppercase;
            }
        }

        &-link {
            text-decoration: none;
            color: #fff;
            opacity: 0.7;

            &:hover {
                opacity: 1;
            }

            & .fab {
                width: 20px;
            }
        }
    }
}

@include rmin($sm-phone) {
    .footer {}
}

@include rmin($tablet) {
    .footer {
        &__list {
            display: flex;
        }

        &__list-item {
            display: flex;
            flex-direction: column;

            padding: 10px;

            &:nth-child(1) {
                width: 100%;
                margin-bottom: 10px;
            }

            &:not(:first-child) {
                width: 25%;
            }
        }

        &__logo {
            width: 50%;
        }

        // sublist
        &__sublist {
            &-item {
                font-size: 16px;

                &:first-child {
                    font-size: 16px;
                }
            }
        }

        &__copyright {
            font-size: 14px;
        }

        &__donate-link {
            padding: 10px 15px;
            font-size: 14px;

            &:hover {
                color: #262626;
            }
        }
    }
}

@include rmin($desktop) {
    .footer {
        padding: 40px 0;

        &__logo {
            width: 95%;
        }

        &__list-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: left;

            padding: 10px;

            &:nth-child(1) {
                width: 30%;
            }

            &:not(:first-child) {
                width: 15%;
            }
        }
    }
}
