@font-face {
  font-family: 'Roboto Regular';
  src: url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: 'Roboto Regular', sans-serif, Arial;
}

.section-title {
  text-align: center;
  font-weight: 700;
}

.section-wrapper {
  margin: 0 auto;
  max-width: 960px;
}

.site-header {
  background: #ffc901;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  height: 100px;
}

.site-header__nav-search-wrapper {
  display: flex;
}

.site-header__search {
  margin-left: 10px;
  margin-right: 75px;
}

.site-header__search-btn {
  display: flex;
}

.site-header__logo-wrapper {
  display: flex;
  margin: 0 40px 0 auto;
}

.site-header__logo {
  display: none;
}

.site-header__logo-phone {
  width: 125px;
}

.site-header__social {
  display: none;
  align-items: center;
}

.site-header__social-link {
  display: flex;
  text-decoration: none;
  font-size: 28px;
  border-radius: 50%;
}

.site-header__social-link i {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
}

.site-header__social-link .fab {
  color: #fff;
}

.site-header__social-link .fab:hover {
  transition: .5s;
  color: #ffc901;
}

.site-header__social-link:hover {
  transition: .5s;
  background-color: #fff;
}

.site-header__donate-link {
  margin-left: 5px;
  padding: 5px 22px;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  text-decoration: none;
  border: 3px solid rgba(255, 255, 255, 0.5);
  border-radius: 25px;
}

.site-header__donate-link:hover {
  color: #ffc901;
  background-color: #fff;
  transition: .5s;
}

@media only screen and (min-width: 680px) {
  .site-header {
    padding: 0 40px 0 0;
  }
  .site-header__logo-wrapper {
    display: flex;
    margin: 0 auto;
  }
  .site-header__logo {
    display: block;
    width: 250px;
    transform: scale(1);
    transition: all 0.3s;
  }
  .site-header__logo-phone {
    display: none;
  }
  .site-header__logo:hover {
    transition: all 0.4s;
    transform: scale(1.1);
  }
  .site-header__social {
    display: flex;
  }
}

@media only screen and (min-width: 1024px) {
  .site-header {
    padding: 0 40px 0 280px;
  }
  .site-header__logo {
    width: 325px;
  }
}

