  height: 25px;
  fill: #ff7900;
  opacity: .5;
}

.player__genre-item-title {
  font-size: 14px;
  margin-top: 5px;
  color: #555555;
}

.station {
  position: relative;
  width: 100%;
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  box-sizing: border-box;
}

.title {
  display: flex;
  font-size: 2vw;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.33);
}

.subtitle {
  display: inline-block;
