  .player__previous-song {
    display: block;
    text-align: center;
    margin-left: 25px;
  }
  .player__previous-song h2 {
    color: #fff;
    font-size: 18px;
    font-weight: 700;
  }
  .player__previous-song p {
    color: #3b3b3b;
    font-size: 14px;
  }
  .player__next-song {
    display: block;
    text-align: center;
    margin-right: 25px;
  }
  .player__next-song h2 {
    color: #fff;
    font-size: 18px;
    font-weight: 700;
  }
  .player__next-song p {
    color: #3b3b3b;
    font-size: 14px;
  }
  .player__genre {
    overflow: hidden;
    display: flex;
    justify-content: center;
    padding: 0;
    margin: auto 0 25px;
  }
  .player__genre-item {
    margin: 0 7px 15px;
  }
}

@media only screen and (min-width: 1024px) {
  .player__genre-item {
    margin: 0 15px 15px;
  }
}

.glide {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

.glide * {
  box-sizing: inherit;
}

.glide__track {
  overflow: hidden;
}

.glide__slides {
  position: relative;
  width: 100%;
  list-style: none;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  touch-action: pan-Y;
  overflow: hidden;
  padding: 0;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  will-change: transform;
}

.glide__slides--dragging {
  user-select: none;
}

.glide__slide {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  white-space: normal;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

.glide__slide a {
  user-select: none;
  -webkit-user-drag: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.glide__arrows {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide__bullets {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide--rtl {
  direction: rtl;
}

.glide__arrow {
  position: absolute;
  display: block;
  top: 50%;
  z-index: 2;
  color: white;
  text-transform: uppercase;
  padding: 9px 12px;
  background-color: transparent;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 4px;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.1);
  text-shadow: 0 0.25em 0.5em rgba(0, 0, 0, 0.1);
  opacity: 1;
  cursor: pointer;
  transition: opacity 150ms ease, border 300ms ease-in-out;
  transform: translateY(-50%);
  line-height: 1;
}

.glide__arrow:focus {
  outline: none;
}

.glide__arrow:hover {
  border-color: white;
}

.glide__arrow--left {
  left: 2em;
}

.glide__arrow--right {
  right: 2em;
}

.glide__arrow--disabled {
  opacity: 0.33;
}

.glide__bullets {
  position: absolute;
  z-index: 2;
  bottom: 2em;
  left: 50%;
  display: inline-flex;
  list-style: none;
  transform: translateX(-50%);
}

.glide__bullet {
  background-color: rgba(255, 255, 255, 0.5);
  width: 9px;
  height: 9px;
  padding: 0;
  border-radius: 50%;
  border: 2px solid transparent;
  transition: all 300ms ease-in-out;
  cursor: pointer;
  line-height: 0;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.1);
  margin: 0 0.25em;
}

.glide__bullet:focus {
  outline: none;
}

.glide__bullet:hover, .glide__bullet:focus {
  border: 2px solid white;
  background-color: rgba(255, 255, 255, 0.5);
}

.glide__bullet--active {
  background-color: white;
}

.glide--swipeable {
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
}

.glide--dragging {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

.slider {
  width: 100%;
  margin: 0 auto;
}

.slider__wrapper {
  width: 100%;
}

.slider__link {
  display: block;
}

.slider__item {
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.slider__glide {
  position: relative;
}

.slider__glide-arrows {
  position: absolute;
  top: 45%;
  width: 100%;
}

.slider__controls {
  position: absolute;
  background-color: transparent;
  cursor: pointer;
}

.slider__controls--left {
  left: 15px;
  transform: rotate(180deg);
}

.slider__controls--right {
  right: 15px;
}

.slider__arrow {
  fill: #fff;
}

@media only screen and (min-width: 680px) {
  .slider__controls--left {
    left: 25px;
  }
  .slider__controls--right {
    right: 25px;
  }
}

.news {
  padding: 0 15px;
  margin-top: 20px;
}

.news__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 15px;
  padding: 15px 0;
}

.news__item {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(173, 173, 173, 0.4);
}

.news__item:hover {
  box-shadow: 0 0 10px rgba(173, 173, 173, 0.5);
}

.news__cover {
  border-radius: 5px 5px 0 0;
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.news__item-wrapper {
  padding: 15px;
}

.news__item-title {
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  line-height: 16px;
  max-height: 32px;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.news__item-text {
  margin: 7px 0 15px 0;
  font-size: 12px;
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  line-height: 16px;
  max-height: 64px;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.news__item-footer {
  display: flex;
  justify-content: space-between;
}

.news__item-date {
  display: flex;
  align-items: center;
  color: var(--gray);
  font-size: 14px;
}

.news__item-icon {
  fill: var(--gray);
  width: 15px;
  height: 15px;
  margin-right: 5px;
}

.news__button-more {
  padding: 8px 25px;
  border: 1px solid var(--light-gray);
  border-radius: 3px;
  color: var(--gray);
  background-color: transparent;
  font-size: 14px;
}

.news__button-more:hover {
  color: #000;
  border: 1px solid rgba(255, 200, 1, 0.5);
  background-color: #ffc901;
  box-shadow: 0 0 10px rgba(255, 200, 1, 0.5);
  transition: .4s;
}

.news__all-button-link {
  display: flex;
  justify-content: center;
  text-decoration: none;
}

.news__all-button {
  padding: 12px 45px;
  font-size: 16px;
  color: var(--dark-gray);
  background-color: var(--light-gray);
  border-radius: 3px;
}

.news__all-button:hover {
  color: #000;
  background-color: #ffc901;
  box-shadow: 0 0 10px rgba(255, 200, 1, 0.5);
  transition: .4s;
}

@media only screen and (min-width: 375px) {
  .news__item-title {
    font-size: 16px;
  }
  .news__item-text {
    font-size: 14px;
  }
  .news__cover {
    height: 200px;
  }
  .news__item-wrapper {
    padding: 20px;
  }
  .news__item-title {
    font-size: 16px;
  }
  .news__item-text {
    font-size: 14px;
  }
}

@media only screen and (min-width: 680px) {
  .news {
    margin-top: 60px;
  }
  .news__cover {
    height: 200px;
  }
  .news__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 25px;
    padding: 25px 0;
  }
}

@media only screen and (min-width: 1024px) {
  .news__list {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

