  height: 100vh;
  margin: 0 auto;
  overflow: hidden;
}

.menu-container {
  background-color: #ffc901;
  border-radius: 100%;
  color: white;
  cursor: pointer;
  position: absolute;
  width: 0;
  height: 0;
  left: -120px;
  top: -120px;
  transition: all 0.3s;
}

.menu-container.full-menu {
  z-index: 100;
  border-radius: 0;
  padding: 0 !important;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  transition: all 0.3s;
  overflow: hidden;
}

.fa-times {
  position: fixed;
}

.full-menu .menu {
  top: 40px;
  left: 40px;
}

.menu {
  color: white;
  font-size: 2.5em;
  position: absolute;
  top: 149px;
  left: 160px;
  z-index: 900;
  transition: all 0.3s;
}

.menu i {
  opacity: 0.8;
  transform: scale(1);
  transition: all 0.3s;
}

.menu i:hover {
  opacity: 1;
  transform: scale(1.2);
  transition: all 0.3s;
}

.overlay {
  margin: 100px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
}

.overlay.open {
  z-index: 100;
  opacity: 1;
  visibility: visible;
  height: 85%;
}

.overlay.open li {
  animation: fadeInRight .5s ease forwards;
  animation-delay: .35s;
}

.overlay.open li:nth-of-type(2) {
  animation-delay: .4s;
}

.overlay.open li:nth-of-type(3) {
  animation-delay: .45s;
}

.overlay.open li:nth-of-type(4) {
  animation-delay: .50s;
}

.overlay nav {
  font-size: 1.8em;
  font-family: "Rubik Mono One", sans-serif;
  position: relative;
  height: 50%;
  top: 50%;
  transform: translateY(-50%);
  font-weight: 400;
  text-align: center;
}

.overlay ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  height: 100%;
}

.overlay ul li {
  display: block;
  height: 20%;
  position: relative;
  opacity: 0;
}

.overlay ul li a {
  display: block;
  position: relative;
  color: #fff;
  text-decoration: none;
  overflow: hidden;
  opacity: 0.7;
  transform: scale(1);
  transition: all 0.3s;
}

.overlay ul li a:hover, .overlay ul li a:focus, .overlay ul li a:active {
  opacity: 1;
  transform: scale(1.2);
  transition: all 0.3s;
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}
