.sponsors {
    margin-top: 15px;
    padding-top: 75px;
    padding-bottom: 25px;

    background-color: var(--light-gray);
    clip-path: polygon(0 20%, 100% 13%, 100% 100%, 0% 100%);

    &__title {
        margin-bottom: 15px;
    }

    &__list {
        display: flex;

        &-item {
            width: 100%;
            opacity: 0.5;
            transition: 0.5s;

            &:hover {
                transition: 0.5s;
                opacity: 0.8;
            }
        }
    }

    &__slider {
        display: none;

        &-arrow {
            fill: var(--dark-gray);
        }

        &-controls {
            &--left {
                left: -45px;
            }

            &--right {
                right: -45px;
            }
        }
    }
}

@include rmin($tablet) {
    .sponsors {
        padding-top: 85px;
        padding-bottom: 35px;
        clip-path: polygon(0 25%, 100% 13%, 100% 100%, 0% 100%);
    }
}

@include rmin($desktop) {
    .sponsors {
        &__slider {
            display: block;
        }
    }
}
